import React, { Component } from "react";
// import Navbar from "./components/Navbar";
// import Title from "./components/Title";
import CharacterPics from "./components/CharacterPics";
import Wrapper from "./components/Wrapper";
import characters from "./characters.json";

class Game extends Component {
    
    
    // Setting this.state.characters to the characters json array
    state = {
      characters
    };
  
    characterSelect = id => {
      // Filter this.state.characters for characters with an id not equal to the id being removed
      const characters = this.state.characters.filter(character => character.id !== id);
      // Set this.state.characters equal to the new characters array
      this.setState({ characters });
    };
  
    // Map over this.state.characters and render a CharacterPics component for each friend object
    render() {
      return (
        <Wrapper>
            {this.state.characters.map(friend => (
            <CharacterPics
              removeFriend={this.removeFriend}
              id={friend.id}
              key={friend.id}
              name={friend.name}
              image={friend.image}
            //   occupation={friend.occupation}
            //   location={friend.location}
            />
          ))}
        </Wrapper>
      );
    }
  }
  
  export default Game;
  